<template>
    <!-- container -->
    <div class="container setting">
        <div class="inner">
                <!-- tab_item 클릭시 active 클래스 토글되는 형태입니다 -->
                <!-- tab_menu -->
                <div class="tab_box type-a">
                    <ul>
                        <li class="tab_item" @click="moveTab('timeSet')"><span>야간시간대 설정</span></li>
                        <li class="tab_item active" @click="moveTab('eventSet')"><span>이벤트 항목 설정</span></li>
                    </ul>
                </div>
                <!-- //tab_menu -->

                <!-- content_box left -->
                <div class="content_box left">

                    <!-- tab_item 클릭시 active 클래스 토글되는 형태입니다 -->
                    <!-- tab_menu -->
                    <div class="tab_box type-b">
                        <ul>
                            <li 
                                class="tab_item"
                                :class="eventSeCode == '002'? 'active' : ''"
                                @click="changeeventSeCode('002')"
                            >
                                <span>점검지</span>
                            </li>
                            <li 
                                class="tab_item"
                                :class="eventSeCode == '003'? 'active' : ''"
                                @click="changeeventSeCode('003')"
                            >
                                <span>오검지</span>
                            </li>
                            <li 
                                class="tab_item"
                                :class="eventSeCode == '004'? 'active' : ''"
                                @click="changeeventSeCode('004')"
                            >
                                <span>환경요소</span>
                            </li>
                        </ul>
                    </div>
                    <!-- //tab_menu -->

                    <div class="tab_cont_box">
                        <div class="tab_cont_item">
                            <span class="tab_item_tit">유형</span>
                            <div class="input_box">
                                <select class="select" v-model="eventTyCode" ref="eventTyCode" @change="changeEventTyCode" id="eventTyCode">
                                    <template
                                        v-for="(evtTy, evtIndx) in getEventTyCodeList"    
                                    >
                                        <option :value="evtTy.code" :key="'ty'+evtIndx" :type="evtTy[eventSeCode]">{{evtTy.name}}</option>
                                    </template>
                                </select>
                            </div>
                        </div>
                        <div class="tab_cont_item">
                            <span class="tab_item_tit">세부유형명</span>
                            <div class="input_box">+
                                <input type="text" class="input_txt" v-model="eventDetailTyNm" ref="eventDetailTyNm" id="eventDetailTyNm" >
                            </div>
                        </div>
                        <div class="tab_cont_item">
                            <span class="tab_item_tit">코드</span>
                            <div class="input_box">
                                <input type="text" class="input_txt" placeholder="2자리의 코드를 숫자로 입력하세요 00~99" id="eventDetailTyCode" v-model="eventDetailTyCode" ref="eventDetailTyCode" maxlength="2">
                            </div>
                        </div>
                    </div>
                    <div class="input_box txt_c">
                        <button class="btn btn_smd btn_primary" @click="fnSave" id="saveBtn">조건 저장</button>
                    </div>
                </div>
                <!-- // content_box left -->

                <!-- content_box right -->
                <div class="content_box right">
                    <div class="fixed_table_box">
                        <div class="table_hd">
                            <!-- 헤더고정 테이블의 경우 헤더와 바디 두개의 테이블의 colgroup 선언을 동일하게 해주세요. -->
                            <table>
                                <colgroup>
                                    <col class="w10p">
                                    <col class="w90p">
                                </colgroup>
                                <tbody>
                                    <tr>
                                        <td>
                                            <span>번호</span>
                                        </td>
                                        <td>
                                            <span>항목명</span>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div class="table_bd scroll_box">
                            <table>
                                <colgroup>
                                    <col class="w10p">
                                    <col class="w90p">
                                </colgroup>
                                <tbody v-if="eventList">                                    
                                    <template
                                        v-if="eventList.length > 0"
                                    >                                        
                                        <tr                                        
                                            v-for="(event,eventIdx) in eventList"
                                            :key="eventIdx"                                                                                        
                                        >
                                            <td>
                                                <span>{{event.rnum}}</span>
                                            </td>
                                            <td>
                                                <span class="float_l pl5 pt5">
                                                    {{getEventTyCodeName(event.eventTyCode)}} /  {{event.eventDetailTyNm}}({{event.eventDetailTyCode}})
                                                </span>                                                
                                                <div class="input_box float_r">
                                                    <button class="btn btn_secondary btn_sm" @click="fnDelete(event)">삭제</button>
                                                </div>
                                                <div class="input_box float_r mr5">
                                                    <button 
                                                        class="btn btn_secondary w40"
                                                        @click="moveCoderOrdr(event,'down')"
                                                        id="downBtn"
                                                    >
                                                     ↓
                                                    </button>
                                                </div>
                                                <div class="input_box float_r mr5">
                                                    <button class="btn btn_secondary w40" @click="moveCoderOrdr(event,'up')" id="upbtn">↑</button> 
                                                </div>
                                            </td>                                         
                                        </tr> 
                                    </template>
                                    <!-- 데이터가 없는 경우 아래 텍스트를 출력하고, 모든 셀은 한줄로 통일합니다. -->                                                                        
                                    <tr v-if="eventList.length == 0">
                                        <td colspan="2">
                                            <span>조건에 부합하는 데이터가 존재하지 않습니다.</span>
                                        </td>
                                    </tr>
                                </tbody>                 
                            </table>

                        </div>

                    </div>
                </div>
                <!-- // content_box right -->
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
    data() {
        return {
            eventSeCode : "002"      // 이벤트구분 코드 
        ,   eventTyCode : "001"      // 이벤트유형코드
        ,   eventDetailTyCode : ""   // 이벤트상세유형코드
        ,   eventDetailTyNm : ""     // 이벤트상세유형명
        ,   orderUpdateList : []
        ,   eventList : null
        ,   eventDetailTyCodeHead : "A"
        ,   eventTyCodeList :       // 유형코드 리스트
            [
                {code : "001" , name : "정지" ,  group : "SCAN", '002': "A", '003':"D"}           // 정지
            ,   {code : "002" , name : "역주행",  group : "SCAN", '002': "B", '003':"E"}         // 역주행
            ,   {code : "003" , name : "보행자",  group : "SCAN", '002': "C" , '003':"F"}         // 보행자
            ,   {code : "004" , name : "CCTV",  group : "ENV", '004': "G"}          // CCTV
            ,   {code : "005" , name : "환경",  group : "ENV", '004': "H"}           // 환경
            ,   {code : "006" , name : "도로상황",  group : "ENV", '004': "I"}       // 도로상황
            ]
        }        
    }
,   computed :{
        // 이벤트 유형 SELECT 박스 정보
        getEventTyCodeList()
        {  
            var list = []
            for(var i in this.eventTyCodeList)
            {
                var obj = this.eventTyCodeList[i];                
                // 환경요소
                if(this.eventSeCode  == "004")
                {
                    if(obj.group == "ENV")
                    {
                        list.push(obj)    
                    }
                }
                else
                {
                    if(obj.group == "SCAN")
                    {
                        list.push(obj)
                    }                    
                }                
            }            
            return list
        }
    }
,   methods :{
        ...mapActions('setting', ['saveEventCodes','getEventCodes','setEventCodeOrderMod','delEventCode'])
        // 설정 탭 메뉴 클릭 이벤트
    ,    moveTab(urlName){
            this.$router.push({name:urlName})
        }     
        // 이벤트 탭 선택 시 
    ,   changeeventSeCode(seCode)
        {        
            this.eventSeCode = seCode;                        
            this.eventList = null;            
            
            if(seCode == "002") this.eventDetailTyCodeHead = "A"
            else if(seCode == '003') this.eventDetailTyCodeHead ="D"
            else this.eventDetailTyCodeHead ="G"

            this.fnSearch();
        }    
    ,   changeEventTyCode(event)
        {   
            //var type = event.target.selectedOptions[0].getAttribute("type")            
            var type = $("#eventTyCode option:selected").attr('type')
            this.eventDetailTyCodeHead = type
            this.fnSearch(this.eventTyCode);
        }
        // form 초기화
    ,   clearForm(evtCode)
        {
            if(this.isNull(evtCode))
            {
                // 환경요소일때 
                if(this.eventSeCode == "004")
                {
                    this.eventTyCode = '004' 
                }
                else
                {
                    this.eventTyCode = '001'
                }
            }
            else {
                this.eventTyCode = evtCode
            }
            
            this.eventDetailTyCode ="";
            this.eventDetailTyNm ="";
        }
    ,   isNull(str)
        {
            if(str == undefined || str == "" || str == null)
            {
                return true;
            }   
            else
            {
                return false;
            }
        }
    ,   fnVali(){
            if(this.isNull(this.eventDetailTyNm))
            {
                this.$info("세부유형명을 입력해주세요");                
                return false;
            }

            if(this.isNull(this.eventDetailTyCode))
            {
                this.$info("코드을 입력해주세요")
                return false;
            }
            else 
            {
                if(this.eventDetailTyCode.length  < 2)
                {
                    this.$info("2자리의 코드로 입력해주세요")
                    return false;
                }
            }

            return true;
        }
    ,   getEventTyCodeName(evtTyCode)    
        {   
            var name = "";
            var list = this.getEventTyCodeList
            for(var i in list)
            {
                var obj = list[i];                
                if(obj.code == evtTyCode)
                {
                    name = obj.name
                    break;
                }
            }
            return name
        }
        // 코드 저장
    ,   async fnSave(){
            if(this.fnVali()==false) return ;
            
            const conResult = await this.$confirm("저장 하시겠습니까?");            
            if(conResult)
            {
                var eventDetailCode = this.eventDetailTyCodeHead+this.eventDetailTyCode
                var params = {
                    eventSeCode         : this.eventSeCode
                ,   eventTyCode         : this.eventTyCode
                ,   eventDetailTyCode   : eventDetailCode
                ,   eventDetailTyNm     : this.eventDetailTyNm
                }                
                const result = await this.saveEventCodes(params);

                if(result.status == 200)
                {
                    
                    if(this.isNull(result.data.errType))
                    {
                        this.$info("저장 되었습니다.","확인");
                        this.fnSearch(this.eventTyCode);
                    }
                    else
                    {
                        this.$info(result.data.message)   
                    }                    
                    
                }
                else
                {                       
                    var errRes = result.data   
                    this.$info(errRes.message)                    
                }
            }
        }
    ,   async fnSearch(evtCode){
            this.clearForm(evtCode);                
            
            var params = {
                eventSeCode         :  this.eventSeCode
            ,   eventTyCode         :  this.eventTyCode
            }
            const result = await this.getEventCodes(params);            
            const resultList = result.data;
            this.eventList = resultList;            
        }
        // 순번 변경 
    ,   async moveCoderOrdr(data,type)
        {    
            if(data.codeOrdr == this.eventList.length && type =="down") return
            if(data.codeOrdr == 1 && type == "up") return            
            var params ={
                eventSeCode : data.eventSeCode
            ,   eventTyCode : data.eventTyCode
            ,   eventDetailTyCode : data.eventDetailTyCode
            ,   codeOrdr : data.codeOrdr
            ,   modeType : type
            }
            const result = await this.setEventCodeOrderMod(params)
            
            this.fnSearch(data.eventTyCode);
        }
        // 항목 삭제 
    ,   async fnDelete(data)
        {
            const conResult = await this.$confirm("해당 세부유형을 삭제시 문제가 될 수 있습니다. \n정말 삭제하시겠습니까?");            
            if(conResult)
            {
                 var params ={
                    eventSeCode : data.eventSeCode
                ,   eventTyCode : data.eventTyCode
                ,   eventDetailTyCode : data.eventDetailTyCode
                ,   codeOrdr : data.codeOrdr                
                }
                const result = await this.delEventCode(params);
                if(result.status == 200)
                {
                    this.$info("해당 세부유형이 삭제되었습니다.");
                    this.fnSearch(this.eventTyCode);
                }
            }
        }
    }
,   mounted(){
        $(".fixed_table_box .table_bd").mCustomScrollbar({
            theme : "minimal-dark"
        });
        this.fnSearch();
    }
,   watch : {
        eventDetailTyCode(val) {
            const reg = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|a-z]/;

            // 한글, 영문 체크
            if(reg.exec(val)!==null) this.eventDetailTyCode = val.replace(/[^0-9]/g,'');

            // .... 만 입력하게 될 경우 체크
            if(isNaN(parseFloat(val))) this.eventDetailTyCode = '';
        }
    }
}
</script>


